<script lang="ts" setup>
import { ElSwitch } from 'element-plus'
import { useI18n } from 'vue-i18n'
const { locale } = useI18n()
const changeLanguage = (val: string | number | boolean) => {
  localStorage.setItem('lang', val as string)
}
</script>
<template>
  <el-switch
    v-model="locale"
    size="large"
    width="80"
    inline-prompt
    style="--el-switch-off-color: var(--el-switch-on-color)"
    active-text="English"
    active-value="en"
    inactive-text="中文"
    inactive-value="zh"
    @change="changeLanguage"
  />
</template>
<style lang="scss" scoped></style>
